<template lang="pug">
.En
  canvas#myCanvas
</template>

<script>
export default {
  data() {
    return {
      canvas: null,
      ctx: null,
      t: 0,
      pos: 0,
      oriX: [],
      oriR: [],
      n: 0,
      posX_del: 0,
      cosX: [],
      sinY: [],
      wave_move: 0,
      bubble_y: 0,
      bubble_r: 20,
    }
  },
  created() {
    for (var i = 0; i < 1000; i++) {
      this.n += Math.random()*10;
      this.oriX.push(this.n);
      this.oriR.push(Math.random()*20);
    };
    
  },
  mounted() {
    this.createCanvas();
    

  },
  methods: {
    createCanvas() {
      this.canvas = document.getElementById("myCanvas");
      this.ctx = this.canvas.getContext('2d');
      this.canvas.width = window.innerWidth;
      this.canvas.height = window.innerHeight;
      this.draw()
      window.requestAnimationFrame(this.createCanvas)
    },
    draw() {
      this.ctx.fillStyle = '#000'
      this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height)
      this.wave();
      this.bubble();
      
    },
    wave() {
      this.ctx.beginPath()
      // this.ctx.moveTo(0, )

      this.ctx.strokeStyle = '#FF206E';
      this.ctx.lineWidth = 5;
      for (let i = 0; i <window.innerWidth+100; i+=50) {
        this.ctx.lineTo(i, Math.sin(i + this.wave_move)*50 + 200)
        if (i < 500 && Math.ceil(this.canvas.height - this.bubble_y -200) < this.wave_move) {
          // this.bubble_y = 500
          this.bubble_r = 500
          // this.bubble()
        }
      }
      this.wave_move += 0.05
      this.ctx.stroke()
      this.ctx.closePath()
    },
    bubble() {
      this.ctx.beginPath()
      this.ctx.globalCompositeOperation = 'destination-atop'

      this.ctx.fillStyle = '#FF206E';
      var r = 20;
      this.ctx.arc(500, this.canvas.height - this.bubble_y, this.bubble_r, 0, Math.PI*2)
      this.ctx.fill()
      this.bubble_y += 1 * this.wave_move;
      // this.ctx.closePath()

    }

  }
}
</script>

<style lang="sass" scoped>
@import '@/assets/mixin.sass'
.En
  width: 100%
  position: absolute
  #myCanvas
    position: absolute


</style>