<template lang="pug">
.Op
  p {{ x }}
  p {{ y }}
  
</template>

<script>
export default {
  data() {
    return {
      pos: null,
      x: null,
      y: null
    }
  },
  mounted() {
    if (window.navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(this.suc, this.err);
      navigator.geolocation.watchPosition(this.suc, this.err)
    }
    else {
      alert("此瀏覽器不支援地理定位功能!");
    }
  },
  methods: {
    suc(evt) {
      this.x = evt.coords.latitude;
      this.y = evt.coords.longitude;
    },
    err() {
      alert("GPS取得失敗");
    }
  }
}
</script>

