<template lang="pug">
.ci
  canvas#circle_transition
</template>

<script>
export default {
  data() {
    return {
      canvas: null,
      ctx: null,
      circle_x: 0,
      circle_path_r: 0,
    }
  },
  mounted() {
    // setInterval(() => {
      this.createCanvas();
    // }, 300)
    
  },
  methods: {
    createCanvas() {
      this.canvas = document.getElementById("circle_transition");
      this.ctx = this.canvas.getContext('2d');
      this.canvas.width = window.innerWidth;
      this.canvas.height = window.innerHeight;
      this.draw()
    },
    draw() {
      this.ctx.save();
      // this.ctx.beginPath()

      this.circle()
      // this.ctx.endPath()

      this.ctx.restore()
      window.requestAnimationFrame(this.draw)
    },
    circle() {
      this.ctx.save();
      this.ctx.fillStyle = '#000';
      this.ctx.translate(this.canvas.width/2, this.canvas.height/2)
      // this.ctx.arc(Math.cos(this.circle_x)*this.circle_path_r/10, Math.sin(this.circle_x)*this.circle_path_r/10, 1, 0, Math.PI*2)
      this.circle_x += 1;
      // this.circle_path_r -= 5;
      for (var i = 0; i < 100; i+= 1) {
        // this.ctx.save();
        this.ctx.beginPath()
        // this.ctx.rotate(1/this.circle_x)

        this.ctx.moveTo(Math.cos(i)*100, Math.sin(i)*100)
        this.ctx.rotate(Math.PI/10)
        this.ctx.lineTo(Math.cos(i)*10/this.circle_x, Math.sin(i)*10/this.circle_x)
        // this.ctx.arc(Math.cos(i)*10/this.circle_x, Math.sin(i)*10/this.circle_x, 1, 0, Math.PI*2)

        this.ctx.fill()
        this.ctx.stroke()
        this.ctx.closePath()

        // this.ctx.endPath()
        // this.ctx.restore();
      }
      // this.ctx.endPath()
      // this.ctx.fill()
      // this.ctx.noStroke()
      // this.ctx.endPath()
      // this.ctx.fill();
      // this.ctx.stroke()
      this.ctx.restore();
      // window.requestAnimationFrame(this.circle)


      
    }
  }
}
</script>

<style lang="sass" scoped>
@import '@/assets/mixin.sass'


</style>